<template>
  <div class="roleDialog">
    <!--新增/修改-->
    <simple-dialog
      :bottonShow="bottonShow"
      :dialog-title="dialogTitle"
      :dialog-visible.sync="dialogVisible"
      dialog-width="1000px"
      save-text="确 定"
      cancel-text="取 消"
      botton-show="true"
      @saveEmit="setRoleAdd"
      @cancelEmit="cancelRoleAdd"
    >
      <el-form
        ref="numberValidateForm"
        :model="roleUser"
        class="form-data-wrap"
        label-width="100px"
        :rules="roleRules"
        :disabled="disableEdit"
      >
        <el-form-item
          v-if="roleOperType !== '3' && roleOperType !== '4'"
          style="margin-top: 10px;"
          label="角色名称："
          prop="name"
        >
          <el-input
            v-model="roleUser.name"
            style="width: 100%;"
            class="input"
            placeholder="请填写角色名称"
          />
        </el-form-item>
        <el-form-item
          v-if="roleOperType !== '3' && roleOperType !== '4'"
          prop="description"
          label="角色描述："
        >
          <el-input
            v-model="roleUser.description"
            style="width: 100%;"
            class="input"
            placeholder="请填写角色描述"
          />
        </el-form-item>
        <el-form-item
          style="margin-bottom: 10px;"
          prop="actions"
          label="角色授权："
        >
          <div class="form-tree-wrap">
            <el-tree
              key="tree"
              ref="tree"
              :data="trees"
              show-checkbox
              node-key="id"
              :default-checked-keys="menus"
              :render-after-expand="false"
              :props="defaultProps"
              @check-change="handleActionsChange"
            >
              <div slot-scope="{ node, data }" class="info-item">
                <div class="label"
                  ><span class="menu-title">{{ node.label }}</span></div
                >
                <div
                  v-if="data.children.length === 0 && data.actions.length !== 0"
                  class="content action"
                >
                  <el-checkbox-group
                    v-model="actionFlag[`menu_${data.id}`]"
                    @change="(val) => data.doOperateChange(val, data.id)"
                  >
                    <el-row v-if="data && data.actions">
                      <el-col
                        v-for="item in data.actions"
                        :key="item.id"
                        class="box-col"
                        :span="6"
                      >
                        <el-checkbox :checked="item.checked" :label="item.id">{{
                          item.name
                        }}</el-checkbox>
                      </el-col>
                    </el-row>
                  </el-checkbox-group>
                </div>
              </div>
            </el-tree>
          </div>
        </el-form-item>
      </el-form>
    </simple-dialog>
  </div>
</template>
<script>
import mixin from '../mixin';
import cloneDeep from 'lodash/cloneDeep';
import {
  queryRoleDetail,
  roleAdd,
  roleEdit,
  menuList
} from '@api/authority.js';
export default {
  name: 'RoleDetail',
  mixins: [mixin],
  props: ['roleOperType'],
  data() {
    return {
      status: false,
      dialogTitle: '新增角色',
      dialogVisible: false,
      roleUser: {
        name: '',
        description: ''
      },
      // roleRules: {
      //   name: [
      //     { required: true, trigger: 'change', message:'角色名称不能为空' },
      //     { max: 20, trigger: ['blur', 'change'], message: '角色名称长度不能超过20个字符' }
      //   ],
      //   description: [
      //     { required: true, trigger: 'change', message: '角色描述不能为空' },
      //     { max: 200, trigger: ['blur', 'change'], message: '角色描述长度不能超过200字符' }
      //   ]
      // },
      roleRules: {
        name: [
          { required: true, trigger: 'change', message: '角色名称不能为空' },
          {
            max: 20,
            trigger: ['blur', 'change'],
            message: '角色名称长度不能超过20个字符'
          }
        ],
        description: [
          { required: true, trigger: 'change', message: '角色描述不能为空' },
          {
            max: 200,
            trigger: ['blur', 'change'],
            message: '角色描述长度不能超过200字符'
          }
        ]
      },
      menus: [],
      defaultProps: {
        children: 'children',
        label: 'title',
        disabled: this.treeDisabledFn
      },
      parentIdList: [],
      bottonShow: true,
      opering: false,
      disableEdit: false //禁止编辑内容
    };
  },
  created() {
    this.initMenu();
  },
  methods: {
    handleOperateChange(opers, menuId) {
      const index = this.menus.indexOf(menuId);
      if (opers && opers.length > 0) {
        //  功能点不为空，要将功能点所属菜单也选中
        if (index === -1) {
          this.opering = true;
          this.$refs.tree.setChecked(menuId, true, false);
        }
      } else {
        // 功能点为空，将所属菜单也反选
        if (index !== -1) {
          this.opering = true;
          this.$refs.tree.setChecked(menuId, false, false);
        }
      }
    },
    queryRoleAuth(row) {
      queryRoleDetail({ roleId: row.id }).then(({ data, error }) => {
        if (!error) {
          this.roleUser.name = data.name;
          this.roleUser.description = data.description;
          this.roleUser.id = row.id;
          const selectedActions = [];
          data.roleRegionVoList.forEach((item) => {
            selectedActions.push(...item.actions);
          });
          this.initActionFlag(selectedActions);
        } else {
          this.$message.error(`获取角色授权信息失败：${error.message};`);
        }
      });
    },
    // 显示判断新增修改弹层
    showAddInfo(type, row, roleOperType) {
      this.dialogVisible = true;
      // 0 新增角色 1编辑角色 2查看详情 3 功能授权-角色授权-角色名称 4角色授权-修改
      if (type && roleOperType === '1') {
        this.dialogTitle = '编辑角色';
        this.bottonShow = true;
        this.queryRoleAuth(row);
      } else if (type && roleOperType === '2') {
        this.bottonShow = false;
        this.dialogTitle = '查看详情';
        this.disableEdit = true;
        this.queryRoleAuth(row);
      } else if (type && roleOperType === '4') {
        this.dialogTitle = '角色授权-修改';
        this.bottonShow = true;
        this.queryRoleAuth(row);
      } else if (!type && roleOperType === '0') {
        this.dialogTitle = '新增角色';
        this.bottonShow = true;
      } else if (!type && roleOperType === '3') {
        this.dialogTitle = '角色授权';
        if (row.id) {
          this.queryRoleAuth(row);
        } else {
          this.roleUser.name = row.name;
          this.roleUser.description = `岗位${name}的内置角色`;
        }
        this.bottonShow = true;
      }
      setTimeout(() => {
        this.status = true;
      }, 1000);
    },
    // 保存角色
    setRoleAdd() {
      this.$refs.numberValidateForm.validate((valid) => {
        if (valid) {
          const data = { ...this.roleUser };

          const as = [];
          Object.keys(this.actionFlag).forEach((key) => {
            as.push(...this.actionFlag[key]);
          });
          data.actions = as;

          let fn = roleEdit;
          let msg = '修改';
          if (this.dialogTitle === '新增角色') {
            fn = roleAdd;
            msg = '新增';
          }
          if (this.roleOperType === '3') {
            data.type = 2;
            if (this.roleUser.id) {
              fn = roleEdit;
            } else {
              fn = roleAdd;
            }
            msg = '授权';
          }
          fn(data).then(({ error, data }) => {
            if (error) {
              this.$message.error(`角色${msg}失败：${error.message}`);
            } else {
              this.$message.success(`角色${msg}成功`);

              if (this.roleOperType === '3') {
                this.$emit('complated', data);
              } else {
                this.$emit('refresh');
              }
              this.cancelRoleAdd();
            }
          });
        } else {
          return false;
        }
      });
    },
    cancelRoleAdd() {
      this.dialogVisible = false;
      this.status = false;
      this.roleUser.name = '';
      this.roleUser.description = '';
      // 取消按钮时清除校验结果
      this.$refs.numberValidateForm.resetFields();
      this.$refs.tree.setCheckedKeys([]);
      this.initActionFlag();
    },
    //菜单初始化
    initMenu() {
      menuList().then(({ data, error }) => {
        if (!error) {
          this.trees = data;
          this.initActionFlag();
        } else {
          this.$message.error(`获取菜单信息失败：${error.message};`);
        }
      });
    },
    initActionFlag(selectedActions) {
      const _self = this;
      const msas = {};
      const sa = [];
      function setActionFlag(list) {
        list.forEach((item) => {
          if (item.children && item.children.length > 0) {
            setActionFlag(item.children);
          } else {
            item.doOperateChange = _self.handleOperateChange;
            const af = [];
            if (item.actions && selectedActions) {
              item.actions.forEach((action) => {
                if (selectedActions.indexOf(action.id) > -1) {
                  af.push(action.id);
                }
              });
              if (af.length > 0) {
                sa.push(item.id);
              }
            }
            msas[`menu_${item.id}`] = af;
          }
        });
      }

      setActionFlag(this.trees);
      this.menus = sa;
      this.actionFlag = cloneDeep(msas);
    },
    getParentNode(currentNode) {
      if (currentNode.level !== 1) {
        this.parentIdList.push(currentNode.parent.data.id);
        this.getParentNode(currentNode.parent);
      }
    },
    treeDisabledFn(val, node) {
      if (node.level === 1) {
        if (val.children && val.children.length) {
          return false;
        } else {
          return true;
        }
      }
    },
    handleActionsChange(data, checked, indeterminate) {
      if (!this.status) {
        return;
      }
      if (checked) {
        if (data.children.length === 0) {
          this.menus.push(data.id);
          //  设置该菜单下所有功能点为全选
          if (!this.opering) {
            // 防止因功能点选中而循环引发事件
            const as = [];
            if (data.actions) {
              data.actions.forEach((action) => as.push(action.id));
              this.actionFlag[`menu_${data.id}`] = as;
            }
          } else {
            this.opering = false;
          }
        }
      } else {
        const index = this.menus.indexOf(data.id);
        if (index > -1) {
          this.menus.splice(index, 1);

          //  取消选中该菜单下所有功能点
          if (!this.opering) {
            this.actionFlag[`menu_${data.id}`] = [];
          } else {
            this.opering = false;
          }
        }
      }
    }
  }
};
</script>
<style lang="scss">
.roleDialog {
  .el-tree
    > .el-tree-node
    > .el-tree-node__children
    > .el-tree-node
    > .el-tree-node__content {
    height: auto;
  }
  .el-tree
    > .el-tree-node
    > .el-tree-node__children
    > .el-tree-node
    > .el-tree-node__children
    .el-tree-node
    > .el-tree-node__content {
    height: auto;
  }
  .info-item {
    display: flex;
    align-items: center;
    width: 100%;
    .label {
      display: block;
      line-height: 26px;
      text-align: left;
      padding: 0 12px 0 0;
      box-sizing: border-box;
      width: 50%;

      .menu-title {
        display: inline-block;
        // width: 100px;
      }
    }
    .content {
      position: relative;
      width: 100%;
      max-height: 100px;
      margin-left: 20px;
      overflow: hidden;
      line-height: 32px;
      &.expand {
        max-height: none;
      }
      &-item {
        flex: 0 0 auto;
        padding: 2px 10px;
        margin-top: 20px;
        margin-right: 40px;
        border: 1px solid #ccc;
        border-radius: 5px;
      }
      &::before,
      &::after {
        display: table;
        content: '';
      }
      &::after {
        clear: both;
      }
      .box-col {
        width: 140px;
        height: 32px;
        margin-left: 20px;

        .el-checkbox {
          .el-checkbox__label {
            width: 135px !important;
            overflow: hidden !important;
            text-overflow: ellipsis !important;
            white-space: nowrap !important;
            vertical-align: middle !important;
          }
        }
      }
    }
    &-add {
      cursor: pointer;
      display: inline-block;
      width: 22px;
      margin-left: 10px;
      text-align: center;
      border: 1px solid rgb(55, 149, 211);
      border-radius: 3px;
    }

    &:before,
    &:after {
      display: table;
      content: '';
    }

    &:after {
      clear: both;
    }
  }
  .form-tree-wrap {
    max-height: 300px;
    overflow-y: scroll;
  }
}
</style>
