import cloneDeep from 'lodash/cloneDeep';
// import vue from 'vue';
import {
  roleQuery,
  userQueryList,
  departmentTree
} from '../../api/authority.js';
import { arrayFind } from '@utils/commonUtil';
import { mapState } from 'vuex';

export default {
  data() {
    return {
      trees: [],
      actionFlag: {},
      roleLoading: false,
      roleList: [],
      roleTotal: null,
      roleQueryStr: '',
      roleCurrpage: 1,
      rolePageSize: 10,
      deptId: '',
      departAll: '',
      departments: [],
      offices: [],
      office: '',
      deptProps: {
        value: 'id',
        label: 'deptName'
      },
      locked: '',
      userQueryStr: '',
      userType: '个人',
      userCurrpage: 1,
      userPageSize: 10,
      userTotal: null,
      userList: [],
      userLoading: false,
      isOne: true,
      roleGroupId: '' // 角色组ID
    };
  },
  computed: {
    ...mapState({
      userId: (state) => state.user.userInfo.id
    })
  },
  methods: {
    getRoleList() {
      this.roleLoading = true;
      const params = {
        page: this.roleCurrpage,
        limit: this.rolePageSize
      };

      if (this.roleQueryStr && this.roleQueryStr !== '') {
        params.queryStr = this.roleQueryStr;
      }
      return new Promise((resolve, reject) => {
        roleQuery(params).then(({ data, error }) => {
          this.roleLoading = false;
          if (!error) {
            this.roleList = data.rows;
            this.roleTotal = data.total;
            resolve();
          } else {
            this.$message.error({
              message: `查询角色失败!${error.message}`
            });
          }
        });
      });
    },
    getUsers() {
      this.userLoading = true;
      const params = { page: this.userCurrpage, limit: this.userPageSize };
      if (this.deptId && this.deptId !== '') {
        params.deptId = this.deptId;
      }

      if (this.locked !== '') {
        params.locked = this.locked;
      }
      if (this.userQueryStr !== '') {
        params.keyWord = this.userQueryStr;
      }
      return new Promise((resolve) => {
        userQueryList(params).then(({ data }) => {
          this.userLoading = false;
          this.userList = data.rows;
          this.userTotal = data.total;

          resolve();
        });
      });
    },
    handleClick(val) {
      this.userCurrpage = val;
      this.getUsers();
    },
    handleSizeChange(val) {
      this.userPageSize = val;
      this.getUsers();
    },
    handleRoleSearchChange() {
      this.roleCurrpage = 1;
      this.getRoleList();
    },
    handleDeptChange(val) {
      const dept = arrayFind(this.departments, 'id', val[0]);
      if (dept && dept.children) {
        this.offices = dept.children;
      } else {
        this.offices = [];
      }
      this.office = '';
    },
    // 部门下拉列表
    initDeptments() {
      departmentTree().then(({ data }) => {
        this.departments = data;
      });
    }
  }
};
