// 判断在当前的page和limit之下，删除某条纪录是否会造成页面无法留在当前页
export function iSsafe4DeleteRow(page, limit, total) {
  let isSafe = true;
  if (total === (page - 1) * limit + 1) {
    isSafe = false;
  }
  return isSafe;
}
